html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'Soyuz Grotesk Bold';
  src: url("../fonts/SoyuzGroteskBold.otf") format("truetype");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}

@font-face {
  font-family: 'HelveticaNeueCyr-Bold';
  src: url("../fonts/HelveticaNeueCyr-Bold.otf") format("truetype");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}

@font-face {
  font-family: 'HelveticaNeueCyr-Roman';
  src: url("../fonts/HelveticaNeueCyr-Roman.otf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'HelveticaNeueCyr-Medium';
  src: url("../fonts/HelveticaNeueCyr-Medium.otf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

.navbar {
  position: fixed;
  top: 0;
  padding: 33px 0px;
  left: 0;
  width: 100%;
  z-index: 101;
  user-select: none;
  transition: background .3s ease;
}

.navbar.filter-open {
  background: white;
  color: #24201E;
}

.navbar__logo {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  font-size: 17px;
  letter-spacing: 0.02em;
  line-height: 88%;
}

.navbar .container {
  display: flex;
  align-items: center;
}

.navbar__left {
  display: flex;
  align-items: center;
  width: 36%;
}

.navbar__filter-current {
  margin-left: 33px;
  border: 1px solid #D3D1CF;
  padding: 12px;
  cursor: pointer;
  position: relative;
  transition: border-color .3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  pointer-events: none;
}

.navbar__filter-current:hover {
  border-color: #205A8E;
}

.navbar__filter-current.active {
  opacity: 1;
  pointer-events: auto;
}

.navbar__filter-current svg {
  margin-right: 8px;
}

.navbar__filter-btn {
  margin-left: 14px;
  border: 1px solid #D3D1CF;
  padding: 10px;
  cursor: pointer;
  position: relative;
  transition: border-color .3s ease;
}

.navbar__filter-btn svg {
  display: block;
}

.navbar__filter-btn:hover {
  border-color: #205A8E;
}

.navbar__filter-btn .close {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  pointer-events: none;
  transition: opacity .2s ease;
}

.navbar__filter-btn .open {
  transition: opacity .2s ease;
}

.navbar__filter-btn.open .close {
  opacity: 1;
}

.navbar__filter-btn.open .open {
  opacity: 0;
}

.navbar__link {
  justify-self: center;
  font-size: 0.9vw;
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
  transition: color .3s ease;
  align-self: flex-start;
  margin-top: 10px;
}

.navbar__link:hover {
  color: #205A8E;
}

.tabs__nav {
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
}

.tabs__nav-item {
  width: 28%;
}

.tabs__content {
  display: flex;
  justify-content: space-between;
}

.tabs__content .tab {
  width: 28%;
  margin-top: 24px;
}

.tabs__item {
  margin-top: 24px;
  cursor: pointer;
  transition: color .3s ease;
}

.tabs__item:hover {
  color: #205A8E;
}

.interview-header {
  height: 100vh;
}

.interview-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  text-align: center;
}

.interview-header .container h1 {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  font-size: 6.45vw;
  line-height: 72%;
  letter-spacing: 0.02em;
}

.interview-header__description {
  margin-top: 70px;
  line-height: 1.4em;
  font-size: 0.9vw;
}

.interview-header__left {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  flex-direction: column;
  height: 100%;
  position: relative;
}

.interview-header__right {
  width: 50%;
  height: 100%;
  padding-top: 40px;
  margin-left: 16px;
}

.interview-header__right img {
  height: 100%;
  display: block;
  float: right;
  width: 100%;
  object-fit: cover;
}

.interview-header__bottom {
  position: absolute;
  bottom: 0;
  padding-bottom: 30px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 0.24em;
  font-size: 0.6vw;
  border-bottom: 1px #D3D1CF solid;
  line-height: 1.6em;
  font-family: 'HelveticaNeueCyr-Roman', 'Arial', sans-serif;
  font-weight: 400;
}

.interview-header__location {
  text-align: left;
}

.interview-header__photograph {
  text-align: right;
}

.article {
  width: 100%;
  margin-top: 15px;
}

.article .container {
  display: flex;
}

.article__left {
  width: 25%;
  border-right: 1px #D3D1CF solid;
  padding-right: 8px;
}

.article__right {
  width: 70%;
  padding-top: 80px;
  font-size: 1.45vw;
  line-height: 1.56em;
  padding-left: 72px;
  padding-bottom: 48px;
}

.article__date {
  font-family: 'HelveticaNeueCyr-Medium', 'Arial', sans-serif;
  font-weight: 500;
  font-size: 1.45vw;
}

.article__social {
  margin-top: 11px;
  display: flex;
  align-items: center;
  border-top: 1px #D3D1CF solid;
}

.article__social-item {
  padding: 17px 24px;
}

.article__social-item:nth-child(2) {
  border-left: 1px #D3D1CF solid;
  border-right: 1px #D3D1CF solid;
}

.article__social-item svg {
  transition: fill .3s ease;
}

.article__social-item.facebook:hover svg {
  fill: #1778F2;
}

.article__social-item.twitter:hover svg {
  fill: #00ACEE;
}

.article__social-item.telegram:hover svg {
  fill: #0088CC;
}

.article__question {
  font-family: 'HelveticaNeueCyr-Medium', 'Arial', sans-serif;
  font-weight: 500;
  margin-top: 40px;
  font-size: inherit;
}

.article__answer {
  margin-top: 40px;
}

.article .interviewer-name, .article .respondent-name {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  display: inline-block;
  margin-right: 80px;
}

[data-router-view="about"] {
  background: #F6F6F3;
}

.about-header {
  height: 66vh;
}

.about-header .container {
  display: flex;
  align-items: center;
  height: 100%;
}

.about-header h1 {
  color: #D3D1CF;
  font-family: 'HelveticaNeueCyr-Roman', 'Arial', sans-serif;
  font-weight: 400;
  line-height: 112%;
  font-size: 3.25vw;
  max-width: 55vw;
}

.with-margin {
  margin-top: 5.2vh;
}

.about-section .container {
  display: flex;
  justify-content: flex-end;
}

.about-section__right {
  width: 70%;
  font-size: 1.45vw;
  line-height: 1.56em;
  display: flex;
  justify-content: space-between;
}

.about-section__left-content {
  width: 40%;
}

.about-section__right-content {
  width: 57%;
}

.about-section__right-content p::first-line {
  margin-left: 80px;
  text-indent: 67px;
}

.team-section {
  background: white;
  margin-top: 128px;
  margin-bottom: 128px;
}

.team-section__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 128px;
  padding-bottom: 128px;
}

.team-section__item {
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.team-section__name {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  margin-top: 40px;
  font-size: 1.25vw;
}

.team-section__occupation {
  margin-top: 12px;
  font-size: 0.9vw;
  opacity: 0.64;
}

.about-footer {
  margin-top: 128px;
  padding-bottom: 24px;
  display: inline-block;
  width: 100%;
}

.about-footer .rights {
  width: 70%;
  float: right;
  display: block;
  color: #D3D1CF;
  font-size: 1.25vw;
}

*::selection {
  background: #205A8E;
  color: white;
}

body {
  overflow: auto;
  height: 100vh;
  font-family: 'HelveticaNeueCyr-Roman', 'Arial', sans-serif;
  font-weight: 400;
  color: #24201E;
}

body.morning {
  background: #fff;
  color: #24201E;
}

body.morning .header__time {
  color: #F6F6F3;
}

body.lunchtime {
  background: #fff;
  color: #24201E;
}

body.lunchtime .header__time {
  color: #205A8E;
}

body.evening {
  background: #F6F6F3;
  color: #24201E;
}

body.evening .header__time {
  color: #fff;
}

body.night {
  background: #24201E;
  color: white;
}

body.night .header__time {
  color: black;
}

body.night .navbar__filter-btn {
  border-color: #205A8E;
}

body.night .navbar__filter-btn .open path {
  fill: white;
}

body.night .navbar__filter-btn:hover {
  border-color: #F6F6F3;
}

.header {
  width: 100%;
  height: 100vh;
  position: fixed;
  overflow: hidden;
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.header::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.header__clock {
  height: 100%;
  position: fixed;
  z-index: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__clock svg {
  height: 86%;
  width: 100%;
}

.header__clock svg path {
  opacity: 0;
}

.header__clock .real {
  display: none;
}

.header__clock .real .line {
  cursor: pointer;
}

.header__clock .real .line:hover path {
  stroke-width: 3px;
}

.header__clock .real path {
  opacity: 1;
  transition: stroke-width .3s ease;
}

.header__clock .real.is-visible {
  display: block;
}

.header__clock .real .active path {
  stroke: #205A8E;
  stroke-width: 3px;
}

.header__clock .real .active {
  stroke: #205A8E;
}

.header__time-wrapper {
  position: fixed;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  height: 9vw;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  width: 100vw;
  user-select: none;
}

.header__time {
  font-size: 18.7vw;
  line-height: 1em;
  transform: translateY(-18%);
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  display: inline-flex;
}

.header__time span {
  display: inline-block;
  transform: translateY(100%);
}

.header__names {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(20%) skewX(-25deg);
  text-align: center;
  width: fit-content;
  z-index: 2;
  user-select: none;
  opacity: 0;
  pointer-events: none;
}

.header__names .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__name-wrapper {
  width: 22vw;
  margin-right: 2.5vw;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  pointer-events: auto;
}

.header__name-wrapper:hover .header__name {
  color: #205A8E;
}

.header__name-wrapper.is-visible {
  display: flex;
}

.header__name-wrapper.is-visible:nth-child(4n+1) {
  margin-top: 33vh;
}

.header__name-wrapper.is-visible:nth-child(4n+2) {
  margin-bottom: 22.3vh;
}

.header__name-wrapper.is-visible:nth-child(4n+3) {
  margin-top: 31vh;
}

.header__name-wrapper.is-visible:nth-child(4n+4) {
  margin-bottom: 20.3vh;
}

.header__name {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 3.25vw;
  line-height: 80%;
  transition: color .3s ease;
}

.header__description {
  margin-top: 16px;
  opacity: 0.6;
  font-size: 1vw;
  padding-left: 40px;
  padding-right: 40px;
}

.footer {
  position: fixed;
  bottom: 5%;
  width: 100%;
  z-index: 2;
  font-size: 0.85vw;
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.footer__left svg {
  margin-left: 5px;
}

.footer__right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer__right span {
  letter-spacing: 0.24em;
  text-transform: uppercase;
  font-size: 0.6vw;
  display: inline-block;
  margin-bottom: 3px;
  font-family: 'HelveticaNeueCyr-Roman', 'Arial', sans-serif;
  font-weight: 400;
}

.footer a {
  transition: color .3s ease;
}

.footer a:hover {
  color: #205A8E;
}

.filter {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100vh;
  background: #F6F6F3;
  align-items: center;
  display: flex;
  opacity: 0;
  pointer-events: none;
  color: #24201E;
  transition: opacity .5s ease;
}

.filter .container {
  display: flex;
  align-items: flex-start;
}

.filter__left {
  width: 36%;
}

.filter__right {
  width: 55%;
}

.filter.open {
  opacity: 1;
  pointer-events: auto;
}

.tabs__nav {
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
}

.tabs__nav-item {
  width: 28%;
}

.tabs__content {
  display: flex;
  justify-content: space-between;
}

.tabs__content .tab {
  width: 28%;
  margin-top: 24px;
}

.tabs__item {
  margin-top: 24px;
  cursor: pointer;
  transition: color .3s ease;
}

.tabs__item:hover {
  color: #205A8E;
}

.container {
  padding-left: 3vw;
  padding-right: 3vw;
  width: 100%;
}

h2 {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 3.25vw;
  line-height: 0.8em;
  transition: color .3s ease;
}

@media (min-width: 1650px) {
  .navbar__filter-btn .open {
    width: 16px;
    height: 16px;
  }
}
